@import "https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;700&display=swap";
* {
  box-sizing: border-box;
}

:root {
  --primary-color: #72318e;
  --secondary-color: #9f44c7;
}

body {
  background-color: var(--primary-color);
  margin: 0;
  font-family: Poppins, sans-serif;
}

header {
  background-color: var(--secondary-color);
  justify-content: flex-end;
  padding: 1rem;
  display: flex;
}

.search {
  border: 2px solid var(--primary-color);
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 50px;
  padding: .5rem 1rem;
  font-family: inherit;
  font-size: 1rem;
}

.search:focus {
  background-color: var(--primary-color);
  outline: 0;
}

.search::-ms-input-placeholder {
  color: #3e1b4d;
  font-weight: 500;
}

.search::placeholder {
  color: #3e1b4d;
  font-weight: 500;
}

main {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

.movie {
  width: 300px;
  background-color: var(--secondary-color);
  border-radius: 3px;
  margin: 1rem;
  position: relative;
  overflow: hidden;
  box-shadow: .2px 4px 5px rgba(0, 0, 0, .2);
}

.movie img {
  width: 100%;
}

.movie-info {
  color: #eee;
  letter-spacing: .5px;
  height: 90px;
  justify-content: center;
  align-items: center;
  padding: 10px 15px;
  display: flex;
}

.movie-info h3 {
  text-align: left;
  flex-grow: 1;
  margin-top: 0;
  margin-bottom: 0;
}

.movie-info span {
  background-color: var(--primary-color);
  border-radius: 3px;
  margin-left: 1rem;
  padding: .25rem .5rem;
  font-weight: bold;
}

.movie-info span.green {
  color: #13dd13;
}

.movie-info span.orange {
  color: #ebe302;
}

.movie-info span.red {
  color: red;
}

.overview {
  max-height: 100%;
  background-color: #fff;
  padding: 1rem;
  transition: transform .3s ease-in;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  transform: translateY(101%);
}

.movie:hover .overview {
  height: 100%;
  cursor: pointer;
  background-color: #cea9df;
  transform: translateY(0);
}

#tags {
  width: 80%;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 10px auto;
  display: flex;
}

.tag {
  color: #fff;
  cursor: pointer;
  background-color: orange;
  border-radius: 50px;
  margin: 5px;
  padding: 10px 20px;
  display: inline-block;
}

.tag:hover, .tag.highlight {
  background-color: #f0550c;
  box-shadow: 0 0 22px rgba(250, 244, 244, .61);
}

.no-results {
  color: #fff;
}

.clear-btn {
  background-color: #1900ff;
  box-shadow: 0 0 22px rgba(250, 244, 244, .61);
}

.clear-btn:hover {
  background-color: #231187;
}

.pagination {
  color: #fff;
  justify-content: center;
  align-items: center;
  margin: 10px 30px;
  display: flex;
}

.page {
  cursor: pointer;
  padding: 20px;
}

.page:hover, .page:focus {
  color: orange;
  scale: 1.2;
}

.page.disabled {
  cursor: not-allowed;
  color: gray;
}

.current {
  width: 60px;
  height: 60px;
  cursor: pointer;
  border: 5px solid orange;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin: 5px;
  font-size: 20px;
  font-weight: 600;
  display: flex;
}

.current:hover {
  background-color: orange;
}

.know-more {
  color: #fff;
  background-color: orange;
  border: 0;
  border-radius: 50px;
  margin-top: 5px;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
}

.overlay {
  height: 100%;
  width: 0;
  z-index: 1;
  background-color: rgba(0, 0, 0, .9);
  transition: all .5s;
  position: fixed;
  top: 0;
  left: 0;
  overflow-x: hidden;
}

.overlay-content {
  width: 100%;
  text-align: center;
  margin-top: 30px;
  position: relative;
  top: 10%;
}

.overlay a {
  color: #818181;
  padding: 8px;
  font-size: 36px;
  text-decoration: none;
  transition: all .3s;
  display: block;
}

.overlay a:hover, .overlay a:focus {
  color: #f1f1f1;
}

.overlay .closebtn {
  font-size: 60px;
  position: absolute;
  top: 20px;
  right: 45px;
}

@media screen and (max-height: 450px) {
  .overlay a {
    font-size: 20px;
  }

  .overlay .closebtn {
    font-size: 40px;
    top: 15px;
    right: 35px;
  }
}

.embed.hide {
  display: none;
}

.embed.show {
  display: inline-block;
}

.arrow {
  font-size: 40px;
  position: absolute;
}

.arrow.left-arrow {
  top: 50%;
  left: 5%;
  transform: translateY(-50%);
}

.arrow.right-arrow {
  top: 50%;
  right: 5%;
  transform: translateY(-50%);
}

.dots {
  margin-top: 30px;
}

.dots .dot {
  border: 5px solid var(--secondary-color);
  color: #fff;
  border-radius: 50%;
  margin: 5px;
  padding: 5px 15px;
  font-size: 20px;
  font-weight: 600;
}

.dots .dot.active {
  border-color: orange;
}

